@import "sass/index";

.container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;

  background: linear-gradient(
    90deg,
    #fedc00 0%,
    #fedb19 6.25%,
    #fed837 12.5%,
    #fed452 18.75%,
    #fecd6b 25%,
    #fec582 31.25%,
    #febb97 37.5%,
    #feafaa 43.75%,
    #ffa1bc 50%,
    #ff92cb 56.25%,
    #ff81d9 62.5%,
    #ff6fe4 68.75%,
    #ff5bee 75%,
    #ff45f5 81.25%,
    #ff2efb 87.5%,
    #ff14fe 93.75%,
    #ff00ff 100%
  );
  border-radius: 20px;

  cursor: pointer;

  @media (max-width: 450px) {
    border-radius: 8px;
  }
}

.disable {
  & * {
    animation: none !important;
  }
}
