@import "sass/index";

@keyframes cupScale {
  0% {
    transform: scale(0.85);
  }

  100% {
    transform: scale(1.05);
  }
}

@keyframes starsScale {
  0% {
    transform: scale(1.15);
  }

  100% {
    transform: scale(0.95);
  }
}

@keyframes salut_1_Scale {
  0% {
    transform: scale(0) translate(0%, 0%) rotate(0deg);
  }

  100% {
    transform: scale(1.5) translate(40%, -100%) rotate(-40deg);
  }
}

@keyframes salut_2_Scale {
  0% {
    transform: scale(0) translate(0%, 0%) rotate(0deg);
  }

  100% {
    transform: scale(1.5) translate(65%, -100%) rotate(-50deg);
  }
}

.lightsElements {
  position: absolute;
}

.lightsDiv {
  $lightsDivColor: #53007f;

  width: 20%;
  height: 65%;
  right: 14%;
  bottom: -20%;

  border-radius: 50%;
  opacity: 0.9;

  background: $lightsDivColor;
  box-shadow: 0px 0px 60px 60px $lightsDivColor;
}

.stars {
  bottom: 0%;
  width: 41% !important;
  right: 4%;
  transform-origin: bottom center;
  animation: starsScale 5s steps(80, end) infinite alternate;
}

.cup {
  bottom: 3%;
  width: 27% !important;
  right: 11%;

  transform-origin: bottom center;
  animation: cupScale 5s steps(80, end) infinite alternate;
}

$salutAnimationCubic: cubic-bezier(0, 0.55, 0.19, 0.2);

.salut_1 {
  bottom: 20%;
  width: 20% !important;
  right: 30%;

  transform-origin: bottom right;
  animation: salut_1_Scale 5s $salutAnimationCubic infinite;
  will-change: transform;
}
.salut_2 {
  bottom: 20%;
  width: 20% !important;
  right: 0%;

  transform-origin: bottom left;
  animation: salut_2_Scale 5s $salutAnimationCubic infinite;
  will-change: transform;
}
