@import "sass/index";

@keyframes buttonScale {
  0% {
    transform: scale(0.85);
  }

  100% {
    transform: scale(1.05);
  }
}

.titleElements {
  position: absolute;
  left: 3%;
}

.title {
  top: 11%;
  width: 40% !important;
}
.subTitle {
  top: 54%;
  width: 48% !important;
}
.button {
  width: 24.5% !important;
  bottom: 10%;

  animation: buttonScale 5s steps(60, end) infinite alternate;
}
